<template>
    <a-button type="info" icon="chevron-left" @click="backHandler" style="margin-left:8px">{{ text }}</a-button>
</template>

<script type="text/javascript">
    import Button from './button.vue';

    export default {
        name: 'goback',
        props: {
            text: {
                type: String,
                default: ''
            },
            to: {
                type: String,
            }
        },
        methods: {
            backHandler() {
                if (this.to) {
                    location.href = this.to;
                } else {
                    history.back();
                }
            }
        },
        components: {
            aButton: Button
        }
    }
</script>



